<template>
  <div class="hello">
    <h2>{{ msg }}</h2>
    <el-form :inline=true ref="form" :model="form"  label-width="150px" class="demo-form-inline">
      <el-form-item label="Cookies数据：">
        <el-input
          :disabled="flag"
          class="inputCookies"
          v-model="form.cookies"
          clearable
          placeholder="Cookies数据为空时，默认使用配置账号"
          @clear="() => { form.cookies=null }"
        ></el-input>
      </el-form-item>
    </el-form>
    <el-form :inline=true :rules="rules" ref="form" :model="form" label-width="100px" class="demo-form-inline">
      <el-form-item label="导出类型：" prop="type">
        <el-select :disabled="flag" v-model="form.type" clearable placeholder="请选择导出类型" class="selectType">
          <el-option
            v-for="item in options"
            :key="item.type"
            :label="item.label"
            :value="item.type"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="项目id：" prop="projectId">
        <el-input :disabled="flag" v-model="form.projectId"
                  clearable
                  placeholder="如：407"
                  class="inputProject"></el-input>
      </el-form-item>
      <el-form-item label="模块名：">
        <el-input :disabled="flag" v-model="form.moduleName"
                  clearable
                  placeholder="如：订单模块"
                  class="inputName"
                  @clear="() => { form.moduleName=null }"></el-input>
      </el-form-item>
    </el-form>
    <el-form>
    <el-form-item>
        <el-button type="primary" :disabled="flag" v-bind:icon="iconData" @click="submitForm('form')">立即导出</el-button>
        <el-button type="primary" :disabled="flag" icon="el-icon-delete" @click="resetForm('form')">重 置</el-button>
    </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      'msg':'欢迎使用接口文档导出工具👏🏻👏🏻👏🏻',
      rules: {
        type: [
          {required: true, message: '请选择导出类型', trigger: 'change'}
        ],
        projectId: [
          {required: true, message: '请输入项目id', trigger: 'blur'}
        ],
      },
      form: {
        cookies: null,
        moduleName: null,
        type: '',
        projectId: ''
      },
      options: [{
        type: 1,
        label: 'Excel'
      }, {
        type: 2,
        label: 'yaml'
      }, {
        type: 3,
        label: 'jmx'
      }],
      iconData: 'el-icon-download',
      flag: false
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.iconData = 'el-icon-loading'
          this.flag = true
          this.$http.post('/rap2/export',this.form).then(res=>{
              if (res.data.code===0){
                this.flag = false
                this.iconData = 'el-icon-download'
                window.open(res.data.data)
                this.$message.success('导出成功')
              }else {
                this.flag = false
                this.iconData = 'el-icon-download'
                this.$message.error(res.data.data)
              }
          })
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.form.moduleName = null;
      this.form.cookies = null;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.inputName {
  width: 200px;
}
.inputProject {
  width: 100px;
}
.inputCookies {
  width: 400px;
}
.selectType {
  width: 150px;
}
</style>



